function sliderCursor() {
    var owl = $('.owl-carousel');

    owl.each(function () {
        var owl_element = $(this);

        if (!owl_element.hasClass('olw-cursors')) {
            owl_element.addClass('olw-cursors');

            $('<a class="gallery-cursor cursor-prev" aria-label="Previous" href="javascript:;"></a>').prependTo(owl_element.parents('.owl-carousel-wrapper'));
            $('<a class="gallery-cursor cursor-next" aria-label="Next" href="javascript:;"></a>').appendTo(owl_element.parents('.owl-carousel-wrapper'));

            owl_element.owlCarousel();

            // disable cursors
            owl_element.on('initialized.owl.carousel changed.owl.carousel refreshed.owl.carousel', function (event) {
                if (!event.namespace) return;
                var carousel = event.relatedTarget,
                    element = event.target,
                    current = carousel.current();

                $('.gallery-cursor.cursor-next', element).toggleClass('disabled', current === carousel.maximum());
                $('.gallery-cursor.cursor-prev', element).toggleClass('disabled', current === carousel.minimum());
            });
            // Go to the next item
            $('.gallery-cursor.cursor-prev').on('click', function () {
                owl_element.trigger('prev.owl.carousel');
            });
            // Go to the previous item
            $('.gallery-cursor.cursor-next').on('click', function () {
                owl_element.trigger('next.owl.carousel');
            });
        }
    });
}

function initMiewFilteredGrid() {
    var grid = document.querySelector('.filtered_grid');
    var $gridElements = $('.listing_divs');
    var detailArea = document.getElementById('detail');

    if ($(grid).length > 0) {
        // Declare any variables we will need as properties of the object
        var $filters = $('.filtered_grid .filters_top'),
            $action = $('.filtered_grid').attr('data-action'),
            $mixer = "",
            groups = [],
            outputString = '',
            inputText,
            $matching = $(),
            listSwitcher = $('.listSwitcher a'),
            previousState = localStorage.getItem('activeCenter');


        if (listSwitcher.length > 0) {
            listSwitcher.on('click', function () {
                var $this = $(this),
                    showEl = '.listing_grid',
                    hideEl = '.listing_list';

                if ($(this).attr('data-switch') == "list") {
                    showEl = '.listing_list',
                        hideEl = '.listing_grid';
                }

                TweenLite.to(hideEl, 1, {
                    y: 80,
                    autoAlpha: 0,
                    ease: Power2.easeOut,
                    onComplete: function () {
                        listSwitcher.removeClass('active');
                        $this.addClass('active');

                        $('.listing_divs').removeClass('grid list');
                        $('.listing_divs').addClass($this.attr('data-switch'));
                        $(hideEl).addClass('hidden');
                        $(showEl).removeClass('hidden');

                        TweenLite.to(showEl, 1, {
                            y: 0,
                            autoAlpha: 1,
                            ease: Power2.easeOut
                        });
                    }
                });
            });
        }

        $filters.find('.field_holder').each(function () {
            var $this = $(this);

            groups.push({
                $inputs: $this.find('.filter'),
                active: '',
                tracker: false
            });
        });

        $('.listings').mixItUp({
            controls: {
                enable: false
            },
            animation: {
                effects: 'fade translateZ(-80px)'
            },
            callbacks: {
                onMixStart: function () {
                    $('#detail').html('');
                    $('.fail-message').fadeOut(200);
                },
                onMixFail: function () {
                    $('.fail-message').fadeIn(200);
                }
            }
        });

        $filters.find('.filter').on('change', function () {
            // loop through each filter group and grap the active filter from each one.
            for (var i = 0, group; group = groups[i]; i++) {
                group.active = [];
                group.$inputs.each(function () {
                    var $this = $(this);

                    if ($this.is('select') && $this.val() != 0 && $this.val() != "") {
                        group.active.push($this.val());
                    }
                });
            }

            outputString = ''; // Reset output string

            for (var i = 0, group; group = groups[i]; i++) {
                outputString += group.active;
            }

            // If the output string is empty, show all rather than none:    
            !outputString.length && (outputString = 'all');

            // Send the output string to MixItUp via the 'filter' method:    
            $('.listings').mixItUp('filter', outputString);
        });

        $("#pesq").keyup(debounce(function () {
            inputText = $("#pesq").val().toLowerCase();
            // Check to see if input field is empty
            if ((inputText.length) > 0) {
                $('.mix').each(function () {
                    var $this = $(this);

                    // add item to be filtered out if input text matches items inside the title   
                    if ($this.find('.searcher-container').length > 0) {
                        $this.find('.searcher-container').each(function () {
                            var matches = false;

                            $(this).find('.searcher').each(function () {
                                if ($(this).text().toLowerCase().includes(inputText)) {
                                    $matching = $matching.add($(this).parents('.mix')[0]);
                                    matches = true;
                                }
                            });

                            if (matches == false) {
                                $matching = $matching.not($(this).parents('.mix')[0]);
                            }
                        });
                    }
                });
                $('.listings').mixItUp('filter', $matching);
            } else {
                // resets the filter to show all item if input is empty
                $('.listings').mixItUp('filter', 'all');
            }
        }, 500));


        $gridElements.each(function (element) {
            $(this).off('click.gridAjax');
            $(this).on('click.gridAjax', function () {
                var postID = $(this).attr('id').split('_');
                var $this = $(this);

                const action = $action == "empresas" ? "getEmpresas" : "getJobs";
                const nonce = $action == "empresas" ? phpVars.empresas_nonce : phpVars.jobs_nonce;

                $.ajax({
                    type: 'post',
                    dataType: 'html',
                    url: phpVars.ajaxurl,
                    data: `action=${action}&id=${postID[1]}&security=${nonce}`,
                    success: function (response) {
                        $('.listing_divs.active').removeClass('active');
                        if ($('.detail').length > 0) {
                            $('.detail').fadeOut('slow');
                        }

                        $this.addClass('active');
                        detailArea.innerHTML = response;

                        initstickyParallax('.detail_div');

                        setTimeout(function () {
                            setStickPosition('.detail_div');
                        }, 300);

                        if ($(detailArea).find('.button').length > 0) {
                            $(detailArea).find('.button').on('click', function () {
                                if (outputString) {
                                    localStorage.setItem('activeCenter', outputString);
                                }
                            });
                        }
                    }
                });
            });
        });

        if ($(grid).hasClass('setFirst')) $('.listing_divs:eq(0)').trigger('click');
        
        if (previousState && previousState!=""){
            localStorage.removeItem('activeCenter');
            $('#center').val(previousState).trigger('change');

            previousState = "";
        }
    }
}
