$(document).ready(function () {
    var initMiewSlider = function () {
        if ($('.banners').length > 0) {//Banners Homepage        
            if ($('.slick-banners').length > 0) {
                if (($('.banners').hasClass('fullscreen') || $('.banners').hasClass('fullscreen-mobile')) && !$('#masthead').hasClass('absolute-start')) {
                    $(window).resized(function () {
                        var $responsiveW = 960;
                        var newH = 'auto';

                        if ($('.banners').hasClass('fullscreen') && $('body').innerWidth() > $responsiveW) {
                            newH = $('#masthead > div').outerHeight() + 'px';
                        } else if ($('.banners').hasClass('fullscreen-mobile') && $('body').innerWidth() <=  $responsiveW) {
                            newH = $('#masthead > div').outerHeight() + 'px';
                        }
                        
                        if (newH !== $('.banners').attr('data-margin')) {
                            $('.banners').attr('data-margin', newH);

                            if (newH != 'auto'){
                                $('.banners').css('height', '-webkit-calc(100vh - ' + newH + ')');
                                $('.banners').css('height', 'calc(100vh - ' + newH + ')');
                            }else{
                                $('.banners').css('height', newH);
                            }
                        }
                    });
                }

                var $dots = false,
                    $arrows = false,
                    $cursors = false,
                    $dots_mobile = false,
                    $arrows_mobile = false,
                    $cursors_mobile = false;

                if ($('.banners').hasClass('has_dots')) $dots = true;
                if ($('.banners').hasClass('has_arrows')) $arrows = true;
                if ($('.banners').hasClass('has_cursors')) $cursors = true;
                if ($('.banners').hasClass('has_dots_mobile')) $dots_mobile = true;
                if ($('.banners').hasClass('has_arrows_mobile')) $arrows_mobile = true;
                if ($('.banners').hasClass('has_cursors_mobile')) $cursors_mobile = true;

                $('.slick-banners').on('init', function (event, slick, currentSlide, nextSlide) {
                    var bgss = new bgsrcset();
                    bgss.callonce = false;
                    bgss.init('.banners_img');
        
                    toogleBanners(0, $('.slick-banners').find('.slick-current'));
                });
                $('.slick-banners').slick({
                    dots: $dots,
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    arrows: $arrows,
                    cursors: $cursors,
                    infinite: false,
                    adaptiveHeight: false,
                    fade: true,
                    autoplay: true,
                    autoplaySpeed: 8000,
                    cssEase: 'linear',
                    responsive: [
                        {
                            breakpoint: 960,
                            settings: {
                                dots: $dots_mobile,
                                arrows: $arrows_mobile,
                                cursors: $cursors_mobile,
                            }
                        }
                    ]
                });
                $('.slick-banners').on('beforeChange', function (event, slick, currentSlide, nextSlide) {
                    toogleBanners(1, $('.slick-banners').find('.slick-current'));

                    if ($('.slick-slide:eq(' + currentSlide + ') video').length > 0) {
                        $('.slick-slide:eq(' + currentSlide + ') video').get(0).pause();
                    }
                });
                $('.slick-banners').on('afterChange', function (event, slick, currentSlide, nextSlide) {
                    toogleBanners(0, $('.slick-banners').find('.slick-current'));

                    if ($('.slick-slide:eq(' + currentSlide + ') video').length > 0) {
                        $('.slick-slide:eq(' + currentSlide + ') video').get(0).play();
                    }
                });

                function toogleBanners(type, $el) {
                    if ($el.length > 0) {
                        var tit = $el.find('.slide-title');
                        var txt = $el.find('.slide-text');
                        var btn = $el.find('a.button');

                        $(tit).toggleClass('banners-show');
                        $(txt).toggleClass('banners-show');
                        $(btn).toggleClass('banners-show');
                    }
                }
            }
        }
    }

    initMiewSlider();
});
function initCentersMap(){
    if ($('#centersMap').length > 0){
        if (!mapboxgl.supported()) {
            console.log('Your browser does not support Mapbox GL');
        }else{
            $('body').addClass('overHidden-mapbox');
            $('#masthead').addClass('stucked');

            var zoom = $('body').innerWidth() > 960 ? 12 : 10;
            var center = $('body').innerWidth() > 960 ? [-8.656973, 41.1621376] : [-8.604742, 41.176834];
            
            var geojson = "";
            $.ajax({
                type: 'post',
                dataType: 'html',
                url: phpVars.ajaxurl,
                data: `action=getCenters&security=${phpVars.centers_nonce}`,
                success: function (response) {
                    //geojson = JSON.parse(response);
                    geojson = JSON.parse(response);

                    mapboxgl.accessToken = 'pk.eyJ1IjoibWlld2RldiIsImEiOiJjanB0dGdpNnkwMG5oM3hxcnBpbXQ5ZnFpIn0.vdBj-XtkNhDfPD2K_8yh1Q';
                    var map = new mapboxgl.Map({
                        container: 'centersMap', // container id
                        style: 'mapbox://styles/miewdev/cjqm8zvrmmwuz2ro7i4w83mzi', //hosted style id
                        center: center,
                        zoom: zoom,
                    });

                    // add markers to map
                    var $i = 1;
                    geojson.features.forEach(function (marker) {
                        // create a DOM element for the marker
                        var el = document.createElement('div');
                        el.className = 'marker listing_title';
                        el.innerHTML = "<span class='text-stroked'>0"+$i+".</span> "+marker.text;

                        el.addEventListener('click', function () {
                            if (!$('#centersPage').hasClass('detail-visible')){
                                //marker.geometry.coordinates[1] -= 0.007;

                                var offset = window.innerWidth <= 960 ? [-150, -100] : [-150, -50];
                                var offsetY = parseInt(($(window).height() / 2) * 0.75);

                                map.flyTo({
                                    center: marker.geometry.coordinates,
                                    offset: [0, -offsetY],
                                    zoom: 14
                                });
                                
                                $('#centersDetail').css('background-image', 'url("' + marker.image + '")');
                                $('#centerDesc').html(marker.message);
                                $('#centerAddress').html(marker.properties.address);
                                $('.marker.active').removeClass('active');
                                $(el).addClass('active clicked');

                                $('.centersBox .button').off('click.closeBox');
                                $('.centersBox .button').on('click.closeBox', function () {
                                    closeMap();
                                });                                
                            }
                        });
                
                        // add marker to map
                        new mapboxgl.Marker(el).setLngLat(marker.geometry.coordinates).addTo(map);
                        $i++;
                    });

                    map.on('moveend', function (e) {
                        if ($('.marker.clicked').length > 0) {
                            map.scrollZoom.disable();
                            map.boxZoom.disable();
                            map.dragPan.disable();
                            map.dragRotate.disable();
                            map.doubleClickZoom.disable();
                            map.touchZoomRotate.disable();

                            var offsetTop = $('.marker.clicked').offset().top + $('.marker.clicked').height();
                            var offsetLeft = window.innerWidth <= 960 ? "50%" : $('.marker.clicked').offset().left + 'px';

                            $('.centersBox').css({
                                'top': offsetTop + 'px',
                                'left': offsetLeft
                            });

                            $('.marker.clicked').removeClass('clicked');
                            $('#centersPage').addClass('detail-visible');
                        } else {
                            //closeMap(); 
                        }
                    });

                    map.on('load', function () {
                        $('#centersDetail').appendTo('.mapboxgl-canvas-container');
                    });
                    map.on('resize', function () {
                        if ($('#centersPage').hasClass('detail-visible')) {
                            var el = $('.marker.active');
                            var offsetTop = $(el).offset().top + $(el).height();
                            var offsetLeft = window.innerWidth <= 960 ? "50%" : $(el).offset().left + 'px';

                            $('.centersBox').css({
                                'top': offsetTop + 'px',
                                'left': offsetLeft
                            });
                        }
                    });

                    if ($('.centers-close-area').length>0){
                        $('.centers-close-area').on('click', function (e) {
                            if ($(e.target).closest('.centersBox').length == 0 && $('#centersPage').hasClass('detail-visible')) {
                                closeMap();                            
                            }
                        });
                    }

                    function closeMap(){
                        map.scrollZoom.enable();
                        map.boxZoom.enable();
                        map.dragPan.enable();
                        map.dragRotate.enable();
                        map.doubleClickZoom.enable();
                        map.touchZoomRotate.enable();

                        $('.centersBox').fadeOut('slow', function () {
                            $('.marker.active').removeClass('active');

                            $('.centersBox').removeAttr('style');
                            $('#centersPage').removeClass('detail-visible').addClass('moving');

                            $('#centersDetail').css('background-image', 'none');
                            $('#centerDesc').html('');
                            $('#centerAddress').html('');

                            map.flyTo({
                                center: center,
                                zoom: zoom,
                            });
                        }); 
                    }
                }
            });
        }
    }
}